<template>
<div id="game">
  <div id="ans" v-if="show" @click="close" class="animate__animated animate__fadeIn">
    <center><div id="ansBlock" class="animate__animated animate__backInDown"><h2 style="color: orange">: </h2>{{ans}}</div></center>
  </div>
	<nav>
    	<h1> </h1>
    	<h3>   </h3>
    	<img src="@/assets/line.png" alt="logo" id="logo" @click="home">
    </nav>
	<div class="task">
    <div class="task" v-if="game == 'money'">
      <div id="wrapTask">
        <center><h2>, , </h2></center>
        <br>
        <span id="list">{{list}}/{{count}}</span>
            7000     10%   (   1   ). <br> 
            " "      ,     50000 ,     61%. <br>
        -          6 .   ,    ,             . <br>
        <br>
        1)      ?<br>
        <br>
         x  - ,        . <br>
        <br>
        2) x. <br>
        3)     : "    ->  x  ->  x ()"?  ,           ?  ,            ?
        <button @click="answer(game, 0)">?</button>
      </div>
    </div>
    <div class="task" v-if="game == 'theory'">
      <div id="wrapTask">
        <center><h2> </h2></center>
        <br>
        <span id="list">{{list}}/{{count}}</span>
                 : Qd=250-4P  Qs=11P-350,  Qd   , Qd   , P       .      ,    ,   . <br> 
                      ,        10  20 .   ,      ,   . <br>
                       . <br>
                 ? <br>
         ,          .        /   ?
        <button @click="answer(game, 0)">?</button>
      </div>
    </div>
  </div>
  <div id="next" @click="next">&#10230;</div>
  <div id="home" @click="home">&#10229;</div>
</div>
</template>

<script>
export default{
  name: 'Game',
  data(){
  	return{
      list: 1,
      count: 1,
      answers: {
        "money": {
          0: "1. 19500 ; 2. 3250 ; 3.          ,     7942,23 .", 
          1: "test"
        },
        "theory": {
          0: " : 40 ;    : 90"
        }
      },
      ans: "",
      show: false
  	}
  },
  computed:{
  	game: function(){
  		return this.$route.params.gameId
  	}
  },
  methods:{
    home: function(){
      this.$router.push('More')
    },
    next: function(id){
      //this.$router.push({name: 'Games', params: {gameId: id}})
    },
    answer: function(id, num){
      this.ans = this.answers[id][num];
      this.show = true;
    },
    close: function(){
      this.show = false;
    }
  },
  mounted(){
  }
}
</script>

<style scoped>
#ans{
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100vw;
  height: 100vh;
  z-index: 99;
  top: 0;
  left: 0;
}
#ansBlock{
  width: 60vw;
  height: 60vh;
  text-align: left;
  transform: translateY(25%);
  color: white;
}
button{
  position: absolute;
  top: 35px;
  right: 35px;
  border-radius: 10px;
  border: none;
  width: 30px;
  height: 30px;
  font-size: 18px;
  background-color: orange;
  color: white;
}
button:hover{
  background-color: #f77d24;
  top: 37px;
}
#list{
  position: absolute;
  top: 38px;
  left: 38px;
}
#wrapTask{
  text-align: left;
  position: absolute;
  height: 60vh;
  width: 80vw;
  top: 50%;
  left: 50%;
  transform: translate(-8%, 0%);
}
h1, h3{
  margin-bottom: auto;
  margin-top: auto;
  font-family: 'Roboto Slab', serif;
}
h1{
  color: black;
  transition: top 1.5s;
  text-shadow: 2px 2px 0 rgb(165, 42, 36), 2px -2px 0 rgb(165, 42, 36), -2px 2px 0 rgb(165, 42, 36), -2px -2px 0 rgb(165, 42, 36), 2px 0px 0 rgb(165, 42, 36), 0px 2px 0 rgb(165, 42, 36), -2px 0px 0 rgb(165, 42, 36), 0px -2px 0 rgb(165, 42, 36), 2px 2px 2px rgba(206,11,11,0);
  font-size: 32px;
}
h3{
  font-size: 20px;
}

</style>


