<template>
  <div id="More">
    <nav>
      <h1> </h1>
      <h3>      </h3>
      <img src="@/assets/line.png" alt="logo" id="logo" @click="home">
    </nav>
    <center><h2> </h2></center>
    <ul>
      <li v-for="item in items" @click="game(item.game)">{{item.name}}</li>
    </ul>
  </div>
</template>

<script>
export default{
  name: 'More',
  props: ['progress'],
  data(){
  	return{
      items: [{'name': '    ', 'game': 'new'}, {'name': '', 'game': 'manage'}, {'name': '', 'game': 'macro'}, {'name': ' ', 'game': 'world'}, {'name': '  ', 'game': 'history'}, {'name': ' ', 'game': 'theory'}, {'name': '', 'game': 'finance'}, {'name': '  ', 'game': 'accounting'}, {'name': ', , ', 'game': 'money'}, {'name': '', 'game': 'metric'}, {'name': '  ', 'game': 'ru'}, {'name': '-', 'game': 'plan'}, {'name': '', 'game': 'invest'}, {'name': ' ', 'game': 'corp'}, {'name': '     ', 'game': 'worldeco'}, {'name': '', 'game': 'market'}, {'name': '  ', 'game': 'counttheory'}, {'name': '   ', 'game': 'report'}, {'name': '  ', 'game': 'analitic'}, {'name': '   ', 'game': 'taxes'}, {'name': '', 'game': 'manage'}, {'name': ' ', 'game': 'safety'}, {'name': '   ', 'game': 'insurace'}, {'name': ' ', 'game': 'finmanage'}, {'name': '  ', 'game': 'neweco'}, {'name': ' :  ', 'game': 'outeco'}, {'name': ' ', 'game': 'base'}, {'name': ' ', 'game': 'corp'}]
  	}
  },
  methods:{
    home: function(){
      this.$router.push('Main')
    },
    game: function(id){
      this.$router.push({name: 'Game', params: {gameId: id}})
    }
  },
  mounted(){
  }
}
</script>

<style scoped>
li{
  color: white;
  margin: 10px;
}
li:hover{
  font-size: 17px;
  margin: 9px;
}
li:hover::marker{
  color: orange !important;
  font-size: 17px !important;
}
ul{
  background-color: black;
  border: 5px solid white;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
}
h1, h3, h2{
  margin-bottom: auto;
  margin-top: auto;
  font-family: 'Roboto Slab', serif;
}
h2{
  color: white;
  text-shadow: 2px 2px 0 black, 2px -2px 0 black, -2px 2px 0 black, -2px -2px 0 black, 2px 0px 0 black, 0px 2px 0 rgb(165, 42, 36), -2px 0px 0 black, 0px -2px 0 black, 2px 2px 2px black;
  font-size: 28px;
}
h1{
  color: black;
  transition: top 1.5s;
  text-shadow: 2px 2px 0 rgb(165, 42, 36), 2px -2px 0 rgb(165, 42, 36), -2px 2px 0 rgb(165, 42, 36), -2px -2px 0 rgb(165, 42, 36), 2px 0px 0 rgb(165, 42, 36), 0px 2px 0 rgb(165, 42, 36), -2px 0px 0 rgb(165, 42, 36), 0px -2px 0 rgb(165, 42, 36), 2px 2px 2px rgba(206,11,11,0);
  font-size: 32px;
}
h3{
  font-size: 20px;
}
</style>


